'use client'

import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { useRouter } from 'next/navigation'
import { observer } from "mobx-react-lite"
import { useStore } from '@/stores/StoreProvider'
import { Eye, EyeOff, TriangleAlert, CircleAlert, CheckCircle } from "lucide-react"
import { Spinner } from "@/components/ui/spinner"
import { toast } from 'sonner'
import { useState } from "react"


const PasswordChangeCard = ({token}) => {

    const { userStore } = useStore()
    const router = useRouter()
    const [formData, setFormData] = useState({
        email: "",
        password: "",
    })
    const [errors, setErrors] = useState({})
    const [showPassword, setShowPassword] = useState(false)


    const handleChange = (e) => {
        const { name, value } = e.target
        setFormData((prev) => ({ ...prev, [name]: value }))
        if (errors[name]) {
            setErrors((prev) => ({ ...prev, [name]: "" }))
        }
    }

    // validation 
    const validate = () => {
        const newErrors = {}

        if (!formData.email.trim()) newErrors.email = "Email is required"
        if (formData.email.length > 0 && !formData.email.includes("@")) newErrors.email = "Invalid email"
        if (!formData.password) newErrors.password = "Password is required"

        return newErrors
    }

    // handle login 
    const handleSubmit = async (e) => {
        e.preventDefault()
        const newErrors = validate();

        if (Object.keys(newErrors).length > 0) {
            setErrors(newErrors)
            return
        }

        await userStore.login(formData)

        if (userStore.error) {

            // if shop not registered 
            if (userStore.error === 'Shop not registered.') {
                localStorage.setItem("signupStep", 2)
                toast.error(userStore.error, {
                    description: 'Please registerd your shop.',
                    action: {
                        label: "Sign up",
                        onClick: () => router.push('/signup'),
                    }
                })

                return
            }

            toast.error(userStore.error)
            return
        }

        toast.success('Login successfully');

        setTimeout(() => {
            router.push(`/shop/${userStore.shopId}`)
        }, 1000);

        setFormData({
            email: "",
            password: "",
        })
    }

    // handle signup 
    const signup = () => {
        localStorage.setItem("signupStep", 1)
        setTimeout(() => {
            router.push('/signup')
        }, 500);

    }

    return (
        <Card className="w-md">
            <div className="p-8">
                <h1 className="text-3xl font-bold text-secondary mb-6">Reset Password</h1>

                <form onSubmit={handleSubmit} className="space-y-4">

                    {/* password  */}
                    <div>
                        <label className="block text-sm font-medium text-primary mb-2">New Password</label>

                        <div className="relative">
                            <Input
                                type={showPassword ? "text" : "password"}
                                name="password"
                                placeholder="Enter your new password"
                                value={formData.password}
                                onChange={handleChange}
                                className={errors.password ? "border-red-500 pr-10" : "border-border pr-10"}
                            />

                            <button
                                type="button"
                                onClick={() => setShowPassword(!showPassword)}
                                className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-600 hover:text-gray-800"
                            >
                                {showPassword ? <EyeOff size={18} /> : <Eye size={18} />}
                            </button>
                        </div>

                        {errors.password && <p className="text-red-600 text-xs mt-1 flex gap-2 items-center"><TriangleAlert className='h-3 w-3' /> {errors.password}</p>}
                    </div>

                    {/* re-enter password  */}
                    <div>
                        <label className="block text-sm font-medium text-primary mb-2">Confirm New Password</label>

                        <div className="relative">
                            <Input
                                type={showPassword ? "text" : "password"}
                                name="password"
                                placeholder="Confirm your new password"
                                value={formData.password}
                                onChange={handleChange}
                                className={errors.password ? "border-red-500 pr-10" : "border-border pr-10"}
                            />

                            <button
                                type="button"
                                onClick={() => setShowPassword(!showPassword)}
                                className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-600 hover:text-gray-800"
                            >
                                {showPassword ? <EyeOff size={18} /> : <Eye size={18} />}
                            </button>
                        </div>

                        {errors.password && <p className="text-red-600 text-xs mt-1 flex gap-2 items-center"><TriangleAlert className='h-3 w-3' /> {errors.password}</p>}
                    </div>


                    <Button type="submit" className="w-full mt-5 hover:cursor-pointer" disabled={userStore.loading}>
                        {userStore.loading ? <><Spinner />Signing in...</> : "Sign In"}
                    </Button>
                </form>
            </div>
        </Card>
    )
}

export default observer(PasswordChangeCard)